import status from '../services/status'

export default {

	data () {
		return {
			statusText:"",
			flag:false,
			back:''
		}
	},

	init () {

	},

	ready () {
		status.setThis.call(status, this);
	},

	methods : {
		backUp () {
			status.backUp();
		}
	},

	initBus (bus) {
	    status.regBus(bus);
	},

	components : {}
}


