import Config from "./config"

export default {
	setThis (self) {
		this.vm = self;
	},

	getText () {
		return {
			"/machineRoom":'',
			"/equipmentCabinet":'',
			"/InstabusEIB":'',
			"/verify":''
		}
	},

	onRouter (r) {
		const self = this.vm ;
		const obj = this.getText();

		if(self){
			self.statusText = obj[r.path]
		}
	},

	backUp () {
		var self = this.vm ;
		self.flag = false;
		Config.Runtime.eventBus.emit("backUp")
	},

	changeState (obj) {
		var self = this.vm ;
		self.flag = true;
		self.back = obj.el ;
	},

	regBus (bus) {

		bus.on("router" , this.onRouter.bind(this))
		bus.on("changeState" , this.changeState.bind(this))
	}
}


