import Config from '../config'

export default {

	setThis (self) {
		this.vm = self;
	},

	gotoCabinet (el) {
		Config.Runtime.eventBus.emit("showCabinet" , {el:el})
		Config.Runtime.eventBus.emit("changeState" , {el:""})  //
	},

	onRouter (r) {

	},

	regBus (bus) {

		bus.on("router" , this.onRouter.bind(this))
	}
}


